'use client'

import React, { useEffect, useState, useCallback } from 'react';
import { Card, CardBody, Container } from "reactstrap";
import axios from 'axios';
import { SpinnerCircular } from 'spinners-react';
import { useTranslation } from 'react-i18next';
import { toast } from 'react-hot-toast';
import type { Booking } from '../../interfaces/booking.interface';

interface BookingsResponse {
  error: boolean;
  message: string;
  data?: Booking[];
}

const Trips: React.FC = () => {
  const { t } = useTranslation();
  const [data, setData] = useState<Booking[]>([]);
  const [loading, setLoading] = useState<boolean>(true);

  const fetchTrips = useCallback(async (): Promise<void> => {
    try {
      const { data } = await axios.get<BookingsResponse>(`api/v1/bookings/user-bookings`);
      if (data?.error) {
        toast.error(data.message);
        setLoading(false);
      } else if (data.data) {
        setData(data.data);
        toast.success(data.message);
        setLoading(false);
      }
    } catch (err: unknown) {
      if (axios.isAxiosError(err)) {
        toast.error(err.message || 'Failed to fetch bookings');
      } else {
        toast.error('An unexpected error occurred');
      }
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    if (typeof document !== 'undefined') {
      document.body.setAttribute('style', 'background: #f1f5f7!important;');
    }
    fetchTrips();
  }, [fetchTrips]);

  if (loading) {
    return (
      <React.Fragment>
        <div className="page-content">
          <Container fluid className="">
            <div className="loader-outer">
              <div className="loader-inner">
                <SpinnerCircular color="#bcc014" size={30} />
              </div>
            </div>
          </Container>
        </div>
      </React.Fragment>
    );
  }

  return (
    <>
      <section className="trip-search mb-5 mt-5">
        <div className="container">
          <div className="row">
            <div className="col-lg-12">
              {data.length === 0 ? (
                <div className="text-center">
                  <p>{t('no_bookings') || 'No bookings found'}</p>
                </div>
              ) : (
                data.map((trip: Booking) => (
                  <Card key={trip.id} className="mb-3">
                    <CardBody>
                      <div className="row">
                        <div className="col-md-6">
                          <h5>{trip.departure_city} â†’ {trip.arrival_city}</h5>
                          <p>{t('journey_date')}: {trip.journey_date}</p>
                          <p>{t('departure_time')}: {trip.departure_time}</p>
                        </div>
                        <div className="col-md-6">
                          <p>{t('ticket_number')}: {trip.ticket_number}</p>
                          <p>{t('status')}: {trip.status}</p>
                          <p>{t('price')}: {trip.price}</p>
                        </div>
                      </div>
                    </CardBody>
                  </Card>
                ))
              )}
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default Trips;
